body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.campo-texto{
    margin: 24px 0;

}

.campo-texto label {
    display: block;
    margin-bottom: 8px;
    font-size: 24px;
}

.campo-texto input {
    background-color: #FFFFFF;
    box-shadow: 10px 10px 30px rgba(0, 0, 0 ,0.06);
    width: 100%;
    border: none;
    font-size: 24px;
    padding: 24px;
    box-sizing: border-box;
}


.formulario {
    display: flex;
    justify-content: center;
    margin: 80px 0;
    box-shadow: 8px 8px 16px rgba(0, 0, 0 ,0.08);
}

.formulario form {
    width: 80%;
    background-color: #f2f2f2;
    border-radius: 1rem;
    padding: 36px 64px;
    box-shadow: 8px 8px 16px rgba(0, 0, 0 ,0.08);
}

.lista-games{
    margin: 24px 0;
}

.lista-games label {
    display: block;
    margin-bottom: 8px;
    font-size: 24px;
}

.lista-games select {
    background-color: #FFFFFF;
    box-shadow: 10px 10px 30px rgba(0, 0, 0 ,0.06);
    width: 100%;
    border: none;
    font-size: 24px;
    padding: 24px;
    box-sizing: border-box;

}


.botao-criar{
    background-color: #6278f7;
    border-radius: 0.5rem;
    font-weight: 700;
    font-size: 18px;
    padding: 32px;
    border: none;
    cursor: pointer;
    color: #ffffff;
    margin: 32px 0;
}

.botao-criar:hover{
    color: #95ffd4;
}
