.card-games-body{
    display: flex;
    flex-flow: column;
    justify-content: space-between;
}
.cards-games{
    display: flex;
    justify-content: space-around;
    padding: 8px 12px;
    box-sizing: border-box;
    border: none;
    box-shadow: 4px 4px 4px rgba(0, 0, 0 ,0.06);
}
.card-games:first-child{
    margin: 8px 0;
}

.card-games h2{
    font-weight: 700;
}
.card-button{
    margin-bottom: 0;
    position: flex-end;
    height: 49px;
    width: 175px;
    display: inline;
    background-color: #6278f7;
    border-radius: 0.5rem;
    font-weight: 700;
    font-size: 18px;
    padding: 8px;
    border: none;
    cursor: pointer;
    color: #ffffff;
    
}
.card-button:hover{
    color: #95ffd4;
}


.banner{
    background-color: #6278f7;
    text-align: center;
    display: inline-block;
    max-height: 800px;

}

.banner img{
    max-width: 100%;
}


.games-section{
    text-align: center;
    padding: 32px;/* 
    background-color: #d9f7e9; */
}

.games-section h3{
    font-size: 32px;
    border-bottom: 4px solid;
    display: inline-block;
    padding: 8px;
}

.games-section .players{
    display: flex;
    justify-content: space-around;
    margin-top: 32px;
    flex-wrap: wrap;
}
.player-card {
    width: 280px;
}

.player-card .cabecalho {
    background-color:  #F0F0F0;
    border-radius: 10px 10px 0px 0px;
    background-image: '';
}
.player-card img {
    width: 100px;
    border-radius: 50%;
    position: relative;
    bottom: -50px;
}

.player-card .rodape {
    background: #FFFFFF;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
    border-radius: 0px 0px 10px 10px;
    padding-top: 90px;
    padding-bottom: 40px;
}

.player-card .rodape h4 {
    color: #6278F7;
    font-size: 18px;
    line-height: 22px;
    font-weight: bold;
    margin-bottom: 8px;
}

.player-card .rodape h5 {
    font-size: 18px;
    line-height: 22px;
    color: #212121;
    padding: 0 16px;
}
