
.lista-games{
    margin: 24px 0;
}

.lista-games label {
    display: block;
    margin-bottom: 8px;
    font-size: 24px;
}

.lista-games select {
    background-color: #FFFFFF;
    box-shadow: 10px 10px 30px rgba(0, 0, 0 ,0.06);
    width: 100%;
    border: none;
    font-size: 24px;
    padding: 24px;
    box-sizing: border-box;

}